
.tsukkomis {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
  padding: 0rem 1rem 0rem 1rem;
}
#timeline {
  position: relative;
  display: table; /* For Firefox */
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5rem;
  text-align: left;
}
#timeline div:after {
  content: '';
  width: 1px;
  position: absolute;
  top: .5rem;
  bottom: 0rem;
  left: 60px;
  z-index: 1;
  background: #c5c5c5;
}



